import { FC, useEffect, useRef } from "react";
import { FieldValues, useForm } from "react-hook-form";
import "./NewsEditorForm.css";
import { NewsItem } from "../types/types";

interface INewsEditorFormProps {
  onSubmit: (formData: FieldValues) => void;
  buttonText: string;
  news?: NewsItem;
}

const NewsEditorForm: FC<INewsEditorFormProps> = (props) => {
  const { onSubmit, buttonText, news } = props;
  const { register, handleSubmit, getValues, setValue } = useForm();
  const modalContentRef = useRef<HTMLDivElement | null>(null); // Ссылка на контейнер модального окна

  // Функция для центрирования поля на экране
  const centerElementOnScreen = (element: HTMLElement) => {
    if (modalContentRef.current) {
      const modalRect = modalContentRef.current.getBoundingClientRect();
      console.log(modalRect);
      const rect = element.getBoundingClientRect();
      const viewportHeight = window.innerHeight;

      const offsetTop = rect.top - modalRect.top + window.scrollY;
      const elementHeight = rect.height;

      // Рассчитываем положение, чтобы элемент оказался в центре экрана
      const centerOffset = (viewportHeight - elementHeight) / 2;
      console.log(offsetTop - centerOffset);

      // Прокручиваем модальное окно, чтобы элемент оказался в центре
      modalContentRef.current.scrollTo({
        top: offsetTop - centerOffset,
        behavior: "smooth",
      });
    }
  };

  // Отслеживаем событие фокуса на любом из полей ввода
  const handleFocus = (
    event: React.FocusEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    centerElementOnScreen(event.target);
  };

  useEffect(() => {
    if (news) {
      setValue("title", news.title || "");
      setValue("multimedia", news.multimedia || "");
    }
  }, [news, setValue]);

  const sub = () => {
    const formData = getValues();
    onSubmit(formData);
  };

  return (
    <div ref={modalContentRef} className="newseditor__form-container">
      <form onSubmit={handleSubmit(sub)} className="newseditor__form">
        <label className="newseditor__label">
          <h3 className="newseditor__input-title">Short Description</h3>
          <textarea
            className="newseditor__input newseditor__input_textarea"
            placeholder="Title"
            {...register("title", {
              required: true,
            })}
            onFocus={handleFocus} // Добавляем обработчик фокуса
          ></textarea>
        </label>
        <label className="newseditor__label">
          <h3 className="newseditor__input-title">Image URL</h3>
          <input
            className="newseditor__input"
            placeholder="Image Link"
            {...register("multimedia", {
              required: true,
            })}
            onFocus={handleFocus} // Добавляем обработчик фокуса
          ></input>
        </label>

        <button type="submit" className="newseditor__submit">
          {buttonText}
        </button>
      </form>
    </div>
  );
};

export default NewsEditorForm;
